:active,
:focus {
  outline: none;
}

h1 {
  font-size: 2rem;
}

.kw-range {
  height: 50px;
  margin: 0 32.5px;
  position: relative;
}

.kw-range .kwr-indicator {
  position: absolute;
  width: 0;
  height: 30px;
  left: -90%;
  top: 0;
}

.kw-range .kwr-indicator::after {
  content: "";
  width: 2px;
  height: 15px;
  background: #bababa;
  position: absolute;
  top: 20px;
  left: -1px;
}

.kw-range .kwr-indicator.motion::after {
  background: #fff;
}

.kw-range .kwr-indicator.motion .kwr-text {
  color: #fff;
}

.kw-range .kwr-text {
  display: inline-block;
  color: #bababa;
  width: 55px;
  position: relative;
  top: 1px;
  left: -27.5px;
}

@media all and (max-width: 480px) {
  .kw-range .kwr-text {
    top: 3px;
  }
}

.kw-range .kwr-line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 4px;
  background: #bababa;
}

.kw-range .kwr-line .kwr-intervals .intv {
  position: absolute;
  top: 0;
  background: #fff;
  height: 100%;
}
